import React, { useLayoutEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import FooterMainPage from "../../FooterMain";
import LoadingBar from "../../component/LoadingBar/pages";
import BlogCard from "./BlogCard";

export default function BlogPage() {
  const navigate = useNavigate();
  const  navigatePortfolio = () => {
    navigate("/");
  };
  const [loading, setLoading] = useState(true);

  useLayoutEffect(() => {
    setLoading(true);
    const timer = setTimeout(() => {
      setLoading(false);
    }, 4000);
    return () => clearTimeout(timer);
  }, []);
  return loading ? <LoadingBar/> : (
    <div>
      <div
        style={{
          height: "100%",
          width: "100%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <div
          style={{
            width: "78%",
          }}
        >
          <div
            style={{
              width: "100%",
              display: "flex",
              justifyContent: "space-between",
            }}
          >
            <div>
              <div>
                <img src="" alt="logo" />
              </div>
              <div>Ravi Prakash</div>
            </div>
            <div>
              <div onClick={navigatePortfolio}>Portfolio</div>
            </div>
          </div>
          <div
            style={{
              width: "100%",
              display: "flex",
              flexDirection: "column",
              alignItems:'center',
              justifyContent: 'center',
              gap: "40px",
            }}
          >
            {/* <div
              style={{
                width: "324px",
                height: "600px",
                backgroundColor: "grey",
              }}
            >
              Hello
            </div>
            <div
              style={{
                width: "684px",
                height: "900px",
                backgroundColor: "red",
              }}
            >
              world
            </div> */}
            <BlogCard/>
            <BlogCard/>
            <BlogCard/>
            <BlogCard/>
          </div>
        </div>
      </div>
      <div>
        <FooterMainPage />
      </div>
    </div>
  );
}
