import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

import "./Style.css";

// import required modules
import { Pagination, Navigation } from "swiper";
import { Card } from "@mui/material";
import TestimonyCard from "../CustomForm/TestimonyCard";

export default function SwiperS() {
  return (
    <div style={{ width: "100%", height: "500px" }}>
      <Swiper
        pagination={{
          type: "fraction",
        }}
        navigation={true}
        modules={[Pagination, Navigation]}
        className="mySwiper"
      >
        <SwiperSlide>
          <TestimonyCard quoteSaid={"Ravi, is very hardworking guy. His strength is to learn things very quickly"} capsName={"RP"} userName={"Ravi Prakash"} job={"SDE, CoC"} post={"Associate"}/>
        </SwiperSlide>
        <SwiperSlide>
        <TestimonyCard quoteSaid={"Ravi, is very hardworking guy. His strength is to learn things very quickly"} capsName={"RP"} userName={"Ravi Prakash"} job={"SDE, CoC"} post={"Associate"}/>
        </SwiperSlide>
        <SwiperSlide>
        <TestimonyCard quoteSaid={"Ravi, is very hardworking guy. His strength is to learn things very quickly"} capsName={"RP"} userName={"Ravi Prakash"} job={"SDE, CoC"} post={"Associate"}/>
        </SwiperSlide>
      </Swiper>
    </div>
  );
}
